import {
  CButton,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CAlert,
} from "@coreui/react";
import axios from "axios";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { selectUser } from "src/localyStore";

const server = process.env.REACT_APP_SERVER;

const Brand = () => {
  const { token } = useSelector(selectUser);
  const [brandList, setBrandList] = useState(null);
  const [modal, setModal] = useState(false);
  const [categories, setCategories] = useState(null);
  const [color, setColor] = useState("");
  const [banner, setBanner] = useState("");
  const [id, setId] = useState("");
  const [brand, setBrand] = useState("");
  const [isUpdateBrand, setIsUpdateBrand] = useState("");
  const [reLoad, setReload] = useState(false);

  // alert
  const [event, setEvent] = useState(null);

  const clearEvent = (ms) => {
    setTimeout(() => {
      setEvent(null);
    }, ms);
  };

  useEffect(() => {
    axios
      .get(server + process.env.REACT_APP_ADMIN_GET_BRAND, {
        headers: {
          authorization: "Bearer " + token,
        },
      })
      .then((response) => {
        console.log(response.data.category);
        setBrandList(response.data.category);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "server-error",
        });
        clearEvent(5000);
      });
    axios
      .get(server + process.env.REACT_APP_ADMIN_All_CATEGORY_GET, {
        headers: {
          authorization: "Bearer " + token,
        },
      })
      .then((response) => {
        setCategories(response.data.categories);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "server-error",
        });
        clearEvent(5000);
      });
  }, [token, reLoad]);

  // create and update brand properties
  const createBrand = () => {
    let data;
    if (!isUpdateBrand) {
      data = {
        brandCategory: brand,
        brandColor: color,
        categoryId: id,
      };
    } else {
      data = {
        brandCategory: isUpdateBrand,
        brandColor: color,
        categoryId: id,
      };
    }
    axios
      .post(server + process.env.REACT_APP_ADMIN_CREATE_BRAND, data, {
        headers: {
          authorization: "Bearer " + token,
        },
      })
      .then((res) => {
        setReload(!reLoad);
        setColor("");
        setBrand("");
        setBanner("");
        setIsUpdateBrand("");
        setModal(false);
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        setModal(false);

        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };
  const updateBrand = (brand) => {
    setColor(brand.brandColor);
    setBrand(brand.brandCategory);
    setIsUpdateBrand(brand.name);
    setId(brand._id);
    setModal(true);
  };
  const closeModal = () => {
    setColor("");
    setBrand("");
    setBanner("");
    setIsUpdateBrand("");
    setModal(false);
  };
  return (
    <div>
      <div className="d-flex align-items-center justify-content-between">
        <h3>Create A Brand</h3>
        <CButton
          color="success"
          variant="outline"
          onClick={() => setModal(true)}
        >
          New Brand
        </CButton>
      </div>
      <hr />
      <br />
      <div className="row align-items-center center">
        <div
          style={{
            width: "200px",
            marginLeft: "-20px",
          }}
        >
          <p className="col">Brand-Color</p>
        </div>
        <p className="" style={{ width: "400px", marginRight: "60px" }}>
          Banner
        </p>
        <p className="col">Category</p>
        <span className="col"></span>
        <span className="col"></span>
      </div>
      <hr />
      {brandList?.map((item) => (
        <div
          key={item._id}
          className="row pt-4 align-items-center center px-2 my-2"
        >
          <div
            className="col row"
            style={{
              width: "200px",
              marginLeft: "10px",
            }}
          >
            <span
              style={{
                height: "20px",
                width: "20px",
                borderRadius: "50%",
                backgroundColor: item.brandColor,
                marginRight: "20px",
              }}
            />
            <p>{item.brandColor}</p>
          </div>
          <div
            className="row d-flex justify-content-center align-items-sm-center"
            style={{ width: "400px" }}
          >
            {item.banner.map((img) => (
              <a href={img.img} alt={img.key} target="_blank">
                <img
                  key={img._id}
                  className="col"
                  // style={{ marginRight: "100px" }}
                  src={img.img}
                  height="50"
                  alt="Banner_logo"
                  loading="lazy"
                />
              </a>
            ))}
          </div>
          <p className="col">{item.name}</p>
          <span className="col">
            <button
              type="button"
              class="btn btn-warning px-4 mr-2"
              onClick={() => updateBrand(item)}
            >
              Edit
            </button>
            <button type="button" class="btn btn-danger px-3">
              Delete
            </button>
          </span>
        </div>
      ))}
      {/* modal for create and update brand category */}
      <div>
        <CModal className="sm-30" show={modal} onClose={closeModal}>
          <CModalHeader closeButton>Brand-Config</CModalHeader>
          <CModalBody>
            {/* input for Banner */}
            {/* <div class="input-group mb-3">
              <div class="custom-file">
                <input
                  type="file"
                  class="custom-file-input"
                  id="inputGroupFile01"
                  value={banner}
                />
                <label class="custom-file-label" for="inputGroupFile01">
                  Choose file
                </label>
              </div>
            </div> */}
            {/* input for brandColors */}
            <div class="input-group mb-3">
              <div class="input-group-prepend">
                <span class="input-group-text" id="basic-addon1">
                  BrandColor
                </span>
              </div>
              <input
                type="color"
                class="form-control"
                placeholder="Username"
                aria-label="Username"
                aria-describedby="basic-addon1"
                value={color}
                onChange={(e) => setColor(e.target.value)}
              />
            </div>

            {/* input for choose categories */}
            <div class="input-group mb-3">
              <select
                onChange={(e) => {
                  let brandDataChoose = JSON.parse(e.target.value);
                  setBrand(brandDataChoose.name);
                  setId(brandDataChoose._id);
                }}
                class="custom-select"
                id="cat"
              >
                <option selected={brand === ""}>
                  {isUpdateBrand ? isUpdateBrand : "Choose..."}
                </option>
                {!isUpdateBrand &&
                  categories?.map((mainCategory) => {
                    if (mainCategory.children[0]) {
                      return mainCategory?.children.map((subCategory) => {
                        if (subCategory.children[0]) {
                          // console.log(subCategory.children);
                          // let newVal = subCategory.children.filter(
                          //   (v, i, a) =>
                          //     a.findIndex((t) => t.name === v.name) === i
                          // );
                          return subCategory.children?.map((item) => {
                            return (
                              <option
                                selected={item.name === brand}
                                key={item._id}
                                value={JSON.stringify(item)}
                              >
                                {item.name}
                              </option>
                            );
                          });
                        }
                      });
                    }
                  })}
              </select>
              <div class="input-group-append">
                <label class="input-group-text" for="cat">
                  Options
                </label>
              </div>
            </div>
          </CModalBody>
          <CModalFooter>
            <CButton onClick={closeModal} color="primary">
              Cancel
            </CButton>
            <CButton
              disabled={
                isUpdateBrand
                  ? brand === "" || color === "" || brand === ""
                  : isUpdateBrand
              }
              onClick={createBrand}
              color="primary"
            >
              Confirm
            </CButton>
          </CModalFooter>
        </CModal>
      </div>
      {event?.type === "error" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="danger">{event.massage}</CAlert>
        </div>
      )}
      {event?.type === "success" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="success">{event.massage}</CAlert>
        </div>
      )}
    </div>
  );
};

export default Brand;
