import React, { useState, useEffect } from "react";
import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CDataTable,
  CRow,
} from "@coreui/react";
import axios from "axios";
import { useSelector } from "react-redux";
import { selectUser } from "src/localyStore";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";

const DriverList = () => {
  const [usersData, setUsersData] = useState(null);
  const { token } = useSelector(selectUser);

  useEffect(() => {
    axios
      .get(process.env.REACT_APP_SERVER + "/api/list/all/drivers", {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((data) => {
        setUsersData(data.data.drivers);
      })
      .catch((err) => {
        console.log(err);
      });
  }, [token]);

  return (
    <CRow>
      <CCol xl={12}>
        <CCard>
          <CCardHeader>
            <h2>Drivers</h2>
          </CCardHeader>
          <CCardBody>
            <CDataTable
              items={usersData}
              fields={[
                "SiNo",
                { key: "name", _classes: "font-weight-bold" },
                "email",
                "phone",
                "registered",
                "role",
                "_",
              ]}
              hover
              itemsPerPage={usersData?.length}
              scopedSlots={{
                _: (item) => (
                  <td
                    onClick={() => alert("delete")}
                    style={{ color: "red", padding: 4 }}
                  >
                    <DeleteForeverIcon />
                  </td>
                ),
                SiNo: (item, index) => <td>{index + 1}</td>,
              }}
            />
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  );
};

export default DriverList;
