import { CCard, CCardTitle, CAlert } from "@coreui/react";
import { FormControlLabel, Switch } from "@material-ui/core";
import axios from "axios";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { selectUser } from "src/localyStore";

const Quiz = () => {
  const { token } = useSelector(selectUser);
  const [question, setQuestion] = useState(null);
  const [isEdited, setIsEdited] = useState(false);
  const [isActive, setIsActive] = useState(true);

  // alert
  const [event, setEvent] = useState(null);

  const clearEvent = (ms) => {
    setTimeout(() => {
      setEvent(null);
    }, ms);
  };
  useEffect(() => {
    axios
      .get(
        process.env.REACT_APP_SERVER +
          process.env.REACT_APP_ADMIN_GET_GAME_QUIZ_PRICE,
        {
          headers: {
            authorization: "Bearer " + token,
          },
        }
      )
      .then((res) => {
        setQuestion(res.data.quiz[0]);
        setIsActive(res.data.quiz[0].isActive);
        setIsEdited(false);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "server-error",
        });
        clearEvent(5000);
      });
  }, [token]);
  // change data
  const change = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    if (name === "question") {
      setQuestion((old) => ({ ...old, question: value }));
    }
    if (name === "options0") {
      question.optionsList[0].option = value;

      setQuestion((old) => ({ ...old, optionsList: question.optionsList }));
    }
    if (name === "options1") {
      question.optionsList[1].option = value;

      setQuestion((old) => ({ ...old, optionsList: question.optionsList }));
    }
    if (name === "options2") {
      question.optionsList[2].option = value;

      setQuestion((old) => ({ ...old, optionsList: question.optionsList }));
    }
    if (name === "options3") {
      question.optionsList[3].option = value;
      setQuestion((old) => ({ ...old, optionsList: question.optionsList }));
    }
    if (name === "rightAnswer") {
      setQuestion((old) => ({ ...old, rightAnswer: value }));
    }
    setIsEdited(true);
  };
  const updatePrizes = () => {
    axios
      .post(
        process.env.REACT_APP_SERVER +
          process.env.REACT_APP_ADMIN_CREATE_GAME_QUIZ_PRICE,
        {
          question: question.question,
          optionsList: question.optionsList,
          rightAnswer: question.rightAnswer,
          isActive: isActive,
        },
        {
          headers: {
            authorization: "Bearer " + token,
          },
        }
      )
      .then((response) => {
        setQuestion(response.data.updatedQuiz);
        setIsEdited(false);
        setEvent({
          type: "success",
          massage: response.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };
  return (
    <div>
      <CCard className="p-4">
        <div className="d-flex align-items-center justify-content-between">
          <CCardTitle>
            <div className="d-flex align-items-center">
              <p className="mr-4 mt-2"> Quiz</p>
              <FormControlLabel
                value={isActive}
                control={
                  <Switch
                    color="success"
                    checked={isActive}
                    onChange={(e) => {
                      setIsEdited(true);
                      setIsActive(e.target.checked);
                    }}
                  />
                }
                label={isActive ? "Active" : "InActive"}
                labelPlacement="end"
              />
            </div>
          </CCardTitle>
        </div>
        <hr />
        <div className="d-flex justify-content-around">
          <div className="">
            <h3>Questions</h3>
          </div>
          <h3>Options</h3>
          <h3>Answer</h3>
        </div>
        <hr />
        <div className="d-flex justify-content-around items-center my-5">
          <div>
            <div className="">
              <textarea
                name="question"
                cols="30"
                rows="3"
                type="text"
                defaultValue={question?.question}
                onChange={change}
              />
            </div>
          </div>
          <div className="d-flex flex-column">
            {question?.optionsList?.map((item, index) => (
              <input
                name={`options${index}`}
                key={item._id}
                className="mb-2"
                defaultValue={item.option}
                onChange={change}
              />
            ))}
          </div>
          <div>
            <input
              defaultValue={question?.rightAnswer}
              onChange={change}
              name="rightAnswer"
            />
          </div>
        </div>
        <button
          disabled={!isEdited}
          type="button"
          class="btn btn-ghost-dark btn-lg"
          onClick={updatePrizes}
        >
          Submit
        </button>
      </CCard>
      {event?.type === "error" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="danger">{event.massage}</CAlert>
        </div>
      )}
      {event?.type === "success" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="success">{event.massage}</CAlert>
        </div>
      )}
    </div>
  );
};

export default Quiz;
