import React, { useState, useEffect } from "react";
import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CDataTable,
  CDropdown,
  CDropdownItem,
  CDropdownMenu,
  CDropdownToggle,
  CRow,
  CAlert,
} from "@coreui/react";
import axios from "axios";
import { useSelector } from "react-redux";
import { selectUser } from "src/localyStore";
import "../product/product.css";
import AssignmentIcon from "@material-ui/icons/Assignment";

const AssineDriver = () => {
  const [products, setProducts] = useState(null);
  const [drivers, setDrivers] = useState("");
  const [loading, setLoading] = useState(true);
  const { token } = useSelector(selectUser);

  // alert
  const [event, setEvent] = useState(null);

  const clearEvent = (ms) => {
    setTimeout(() => {
      setEvent(null);
    }, ms);
  };

  useEffect(() => {
    axios
      .get(process.env.REACT_APP_SERVER + "/api/list/ordered-product", {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        setProducts(res.data.productItems);
        setLoading(false);
        console.log(res);
      })
      .catch((err) => {
        setLoading(false);
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "error on server",
        });
        clearEvent(5000);
      });
    axios
      .get(process.env.REACT_APP_SERVER + "/api/list/all/drivers", {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((data) => {
        setDrivers(data.data);
      });
  }, [token]);

  const assineDriver = (driverId, ProductId) => {
    axios
      .post(
        process.env.REACT_APP_SERVER + "/api/assign/driver",
        {
          driver: driverId,
          productItems: {
            product: ProductId,
          },
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      )
      .then((res) => {
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        setLoading(false);
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "error on server",
        });
        clearEvent(5000);
      });
  };

  return (
    <CRow>
      <CCol xl={12}>
        <CCard>
          <CCardHeader>
            <h2>AssignDriver</h2>
          </CCardHeader>
          <CCardBody>
            <CDataTable
              items={products}
              fields={[
                {
                  key: "SiNo.",
                  label: "",
                  _style: { width: "6%" },
                  sorter: false,
                  filter: false,
                },
                {
                  key: "ProductImg",
                  sorter: false,
                  filter: false,
                },
                "name",
                "price",
                "quantity",
                {
                  key: "Action",
                  label: "",
                  _style: { width: "12%" },
                  sorter: false,
                  filter: false,
                },
              ]}
              tableFilter
              columnFilter
              sorter
              pagination
              itemsPerPageSelect
              loading={loading}
              hover
              itemsPerPage={20}
              scopedSlots={{
                "SiNo.": (item, index) => <td key={index}>{index + 1}</td>,
                ProductImg: (item) => (
                  <td key={item._id}>
                    <img
                      className="productlist_img"
                      src={item.productPictures[0].img}
                      alt={item.name}
                    />
                  </td>
                ),
                Action: (item) => (
                  <td key={item._id}>
                    <CDropdown>
                      <CDropdownToggle>
                        <AssignmentIcon />
                      </CDropdownToggle>
                      <CDropdownMenu>
                        {drivers?.drivers?.map((driver) => (
                          <CDropdownItem
                            key={driver._id}
                            onClick={() => assineDriver(driver._id, item._id)}
                          >
                            {driver.name}
                          </CDropdownItem>
                        ))}
                      </CDropdownMenu>
                    </CDropdown>
                  </td>
                ),
              }}
            />
          </CCardBody>
        </CCard>
      </CCol>
      {event?.type === "error" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="danger">{event.massage}</CAlert>
        </div>
      )}
      {event?.type === "success" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="success">{event.massage}</CAlert>
        </div>
      )}
    </CRow>
  );
};

export default AssineDriver;
