import {
  CButton,
  CDataTable,
  CInput,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CAlert,
} from "@coreui/react";
import { Add, Clear } from "@material-ui/icons";
import ReplaySharp from "@material-ui/icons/ReplaySharp";
import axios from "axios";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { useHistory } from "react-router";
import { selectUser } from "src/localyStore";
import { ImageHandler } from "src/reusable/imageHandeller";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";
import CreateIcon from "@material-ui/icons/Create";
const server = process.env.REACT_APP_SERVER;

const EditCategory = (props) => {
  const { id } = props.match.params;
  // Admin token
  const history = useHistory();
  const { token } = useSelector(selectUser);
  const [loading, setLoading] = useState(true);

  const [updateModal, setUpdateModal] = useState(false);
  const [newCategoryModal, setNewCategoryModal] = useState(false);
  // new
  const [newCategoriesName, setNewCategoriesName] = useState("");
  const [newCategoriesIndex, setNewCategoriesIndex] = useState(1);
  const [newCategoriesImg, setNewCategoriesImg] = useState("");
  const [newCategoryMBanner, setNewCategoryMBanner] = useState("");
  const [newCategoryWBanner, setNewCategoryWBanner] = useState("");
  const [newCategoriesType, setNewCategoriesType] = useState("list");
  const [seoTitle, setSeoTitle] = useState("");
  const [seoDescription, setSeoDescription] = useState("");
  const [seoAlt, setSeoAlt] = useState("");
  const [color, setColor] = useState();

  // alert
  const [event, setEvent] = useState(null);

  const clearEvent = (ms) => {
    setTimeout(() => {
      setEvent(null);
    }, ms);
  };

  // ..end new

  const [isUpdate, setIsUpdate] = useState(false);
  const [reload, setReload] = useState(false);
  const [brandModal, setBrandModal] = useState(false);
  const [childs, setchilds] = useState([]);
  // set Tables
  const [category, setCategory] = useState({
    categoryIcon: "",
    categoryImage: "",
    _id: "",
    createdAt: "",
    createdBy: "",
    index: 0,
    name: "",
    parentId: "",
    seoAlt: "",
    seoDescription: "",
    seoTitle: "",
    slug: "",
    type: "list",
    identity: "",
  });
  useEffect(() => {
    axios
      .get(server + process.env.REACT_APP_ADMIN_CATEGORY_GET_ID + id, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        setCategory(res.data.categories);
        setchilds(res.data.children);
        setLoading(false);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  }, [reload, token, id]);

  // submit new categories
  const confirmDeleteCategories = (idDelete) => {
    axios
      .delete(server + process.env.REACT_APP_ADMIN_CATEGORY_DELETE + idDelete, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        setReload(!reload);
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };
  // add new category
  // addnew category
  const addNewCategories = () => {
    if (
      !newCategoriesName ||
      !newCategoriesIndex ||
      !newCategoriesImg ||
      !seoTitle ||
      !seoDescription ||
      !seoAlt
    ) {
      setEvent({
        type: "warn",
        massage: "every feild is required",
      });
      clearEvent(5000);
      return;
    }
    const formData = new FormData();

    formData.append("name", newCategoriesName);
    formData.append("parentId", category?._id);
    formData.append("identity", category?.identity);
    formData.append("index", newCategoriesIndex ?? 0);
    formData.append("categoryImage", newCategoriesImg);
    if (newCategoryWBanner) {
      formData.append("webBanner", newCategoryWBanner);
    }
    if (newCategoryMBanner) {
      formData.append("mobBanner", newCategoryMBanner);
    }
    formData.append("type", newCategoriesType ? newCategoriesType : "list");
    formData.append("seoTitle", seoTitle);
    formData.append("seoDescription", seoDescription);
    formData.append("seoAlt", seoAlt);
    axios
      .post(
        process.env.REACT_APP_SERVER +
          process.env.REACT_APP_ADMIN_CATEGORY_POST,
        formData,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      )
      .then((res) => {
        setNewCategoriesName("");
        setNewCategoriesIndex("");
        setSeoDescription("");
        setNewCategoriesType("");
        setSeoTitle("");
        setSeoAlt("");
        setReload(!reload);
        setNewCategoryModal(false);
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };
  // ....end new
  // update category confirm
  const updateCategory = () => {
    setUpdateModal(!updateModal);
  };
  // update categories
  //update later
  const updateCategories = () => {
    category.categoryId = category._id;
    axios
      .put(
        server + process.env.REACT_APP_ADMIN_CATEGORY_UPDATE,
        { ...category },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      )
      .then((res) => {
        setReload(!reload);
        setUpdateModal(false);
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };
  //   add new
  const NewCategoryModalToggle = () => {
    setNewCategoryModal(!newCategoryModal);
  };

  // brand
  const toggleBrand = () => {
    setBrandModal(!brandModal);
  };

  // image change
  const replaceImage = async (name, type, img, deleteId) => {
    let result;
    if (type === "add") {
      result = await ImageHandler(id, "category", name, img, null, token);
    } else if (type === "delete") {
      result = await ImageHandler(id, "category", name, null, deleteId, token);
    } else {
      result = await ImageHandler(id, "category", name, img, null, token);
    }
    setReload(!reload);
    setEvent({
      type: "success",
      massage: "img success",
    });
    clearEvent(5000);
  };
  // brand
  const createBrand = () => {
    axios
      .post(
        server + process.env.REACT_APP_ADMIN_CREATE_BRAND,
        {
          brandCategory: category.name,
          brandColor: color,
          categoryId: id,
        },
        {
          headers: {
            authorization: "Bearer " + token,
          },
        }
      )
      .then((res) => {
        setReload(!reload);
        toggleBrand();
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        toggleBrand();

        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };

  return (
    <div>
      <h3 className="center">
        {category?.parentId === "" ? "Main Categories" : "Categories"}
      </h3>
      <CButton
        onClick={() => {
          confirmDeleteCategories(id);
          category.parentId
            ? history.push("/categoriesList")
            : history.push("/categoriesList/" + category.parentId);
        }}
        style={{ float: "left" }}
        color="danger"
        variant="outline"
      >
        Delete
      </CButton>
      {/* if sub-2 only create brand and hide new */}
      {category?.identity === "sub2Category" && (
        <CButton
          onClick={toggleBrand}
          style={{
            float: "right",
            cursor: category.isBrand ? "not-allowed" : "pointer",
          }}
          color="success"
          disabled={category.isBrand}
        >
          Make Brand
        </CButton>
      )}
      {category?.identity !== "sub2Category" && (
        <CButton
          onClick={NewCategoryModalToggle}
          style={{ float: "right" }}
          color="success"
          variant="outline"
        >
          Add Subcategory
        </CButton>
      )}
      <br />
      <hr />
      <div className="d-flex align-items-start justify-content-end flex-row-reverse">
        <div className="w-25">
          {category?.identity !== "mainCategory" && (
            <div className="form-group">
              <h4 className="mr-4">CategoryImage : </h4>
              <div className="d-flex">
                {category?.categoryIcon === ""
                  ? ""
                  : category?.categoryImage?.map(
                      (item) =>
                        item && (
                          <div className="" key={item._id}>
                            <a href={item.img} target="_blank" rel="noreferrer">
                              <img
                                width="100"
                                src={item.img}
                                alt="icon"
                                loading="lazy"
                              />
                            </a>
                          </div>
                        )
                    )}
                <div>
                  <CButton
                    className="flex align-items-center justify-content-center mx-4 pt-2 px-2"
                    color="info"
                    size="sm"
                    shape="pill"
                  >
                    <label htmlFor="categoryImage">
                      <ReplaySharp fontSize="small" />
                    </label>
                  </CButton>
                </div>
              </div>
              <input
                hidden
                type="file"
                accept="image/png, image/jpeg,image/jpg,image/svg,image/webp"
                name="categoryImage"
                className="form-control-file"
                id="categoryImage"
                onChange={(e) =>
                  replaceImage(
                    "categoryImage",
                    "replace",
                    e.target.files[0],
                    category?.categoryImage[0]?._id
                  )
                }
              />
            </div>
          )}
          {category?.identity === "mainCategory" && (
            <div className="form-group">
              <h4 className="mr-4">CategoryIcon</h4>
              <div className="d-flex">
                {category?.categoryIcon === ""
                  ? ""
                  : category?.categoryIcon?.map(
                      (item) =>
                        item && (
                          <div className="" key={item._id}>
                            <a href={item.img} target="_blank" rel="noreferrer">
                              <img
                                width="50"
                                src={item.img}
                                alt="icon"
                                loading="lazy"
                              />
                            </a>
                          </div>
                        )
                    )}
                <div>
                  <CButton
                    className="flex align-items-center justify-content-center mx-4 pt-2 px-2"
                    color="info"
                    size="sm"
                    shape="pill"
                  >
                    <label htmlFor="categoryIcon">
                      <ReplaySharp fontSize="small" />
                    </label>
                  </CButton>
                </div>
              </div>
              <input
                hidden
                type="file"
                accept="image/png, image/jpeg,image/jpg,image/svg,image/webp"
                name="categoryIcon"
                className="form-control-file"
                id="categoryIcon"
                onChange={(e) =>
                  replaceImage(
                    "categoryIcon",
                    "replace",
                    e.target.files[0],
                    category?.categoryIcon[0]?._id
                  )
                }
              />
            </div>
          )}
          {category?.identity === "sub2Category" && (
            <>
              <div className="form-group">
                <h4 className="mr-4">Web-Banner : </h4>
                <div className="d-flex flex-wrap">
                  {category?.webBanner === ""
                    ? ""
                    : category?.webBanner?.map(
                        (item) =>
                          item && (
                            <div
                              className="m-2 d-flex align-items-center justify-content-center"
                              style={{
                                position: "relative",
                              }}
                              key={item._id}
                            >
                              <a
                                href={item.img}
                                target="_blank"
                                rel="noreferrer"
                              >
                                <img
                                  width="80"
                                  src={item.img}
                                  alt="icon"
                                  loading="lazy"
                                />
                              </a>
                              <div
                                style={{
                                  position: "absolute",
                                  top: 0,
                                  right: -4,
                                }}
                                onClick={() =>
                                  replaceImage(
                                    "webBanner",
                                    "delete",
                                    null,
                                    item.keys
                                  )
                                }
                              >
                                <div className="p-1 bg-danger text-white rounded-pill">
                                  <Clear fontSize="small" />
                                </div>
                              </div>
                            </div>
                          )
                      )}
                  <div>
                    <CButton
                      className="flex align-items-center justify-content-center mx-4 pt-2 px-2"
                      color="info"
                      size="sm"
                      shape="pill"
                    >
                      <label htmlFor="web">
                        <Add fontSize="medium" />
                      </label>
                    </CButton>
                  </div>
                </div>
                <input
                  hidden
                  onChange={(e) =>
                    replaceImage("webBanner", "add", e.target.files[0])
                  }
                  type="file"
                  accept="image/png, image/jpeg,image/jpg,image/svg,image/webp"
                  name="categoryBanner"
                  className="form-control-file"
                  id="web"
                />
              </div>
              <div className="form-group">
                <h4 className="mr-4">Mobile-Banner : </h4>
                <div className="d-flex flex-wrap">
                  {category?.banner === ""
                    ? ""
                    : category?.mobBanner?.map(
                        (item) =>
                          item && (
                            <div
                              className="m-2 d-flex align-items-center justify-content-center"
                              style={{
                                position: "relative",
                              }}
                              key={item._id}
                            >
                              <a
                                href={item.img}
                                target="_blank"
                                rel="noreferrer"
                              >
                                <img
                                  width="80"
                                  src={item.img}
                                  alt="icon"
                                  loading="lazy"
                                />
                              </a>
                              <div
                                style={{
                                  position: "absolute",
                                  top: 0,
                                  right: -4,
                                }}
                                onClick={() =>
                                  replaceImage(
                                    "mobBanner",
                                    "delete",
                                    null,
                                    item.keys
                                  )
                                }
                              >
                                <div className="p-1 bg-danger text-white rounded-pill">
                                  <Clear fontSize="small" />
                                </div>
                              </div>
                            </div>
                          )
                      )}
                  <div>
                    <CButton
                      className="flex align-items-center justify-content-center mx-4 pt-2 px-2"
                      color="info"
                      size="sm"
                      shape="pill"
                    >
                      <label htmlFor="mob">
                        <Add fontSize="medium" />
                      </label>
                    </CButton>
                  </div>
                </div>
                <input
                  hidden
                  onChange={(e) =>
                    replaceImage("mobBanner", "add", e.target.files[0])
                  }
                  type="file"
                  accept="image/png, image/jpeg,image/jpg,image/svg,image/webp"
                  name="categoryBanner"
                  className="form-control-file"
                  id="mob"
                />
              </div>
            </>
          )}
        </div>
        <div className="w-75">
          {/* end img */}
          <CInput type="hidden" name="id" Value={category?._id} />
          Category Name:
          <CInput
            className="w-25"
            onChange={(e) => {
              setIsUpdate(true);
              setCategory((old) => ({ ...old, name: e.target.value }));
            }}
            type="text"
            name="name"
            value={category?.name}
          />
          Category Index:
          <CInput
            className="w-25"
            onChange={(e) => {
              setIsUpdate(true);
              setCategory((old) => ({ ...old, index: e.target.value }));
            }}
            type="number"
            min={0}
            name="index"
            value={category?.index}
            placeholder="index"
          />
          SEOTitle :
          <CInput
            className="w-25"
            onChange={(e) => {
              setIsUpdate(true);
              setCategory((old) => ({
                ...old,
                seoTitle: e.target.value,
              }));
            }}
            type="text"
            name="seoTitle"
            value={category?.seoTitle}
          />
          SEODescription :
          <CInput
            className="w-25"
            onChange={(e) => {
              setIsUpdate(true);
              setCategory((old) => ({
                ...old,
                seoDescription: e.target.value,
              }));
            }}
            type="text"
            name="seoDescription"
            value={category?.seoDescription}
          />{" "}
          SEOAlt :
          <CInput
            className="w-25"
            onChange={(e) => {
              setIsUpdate(true);
              setCategory((old) => ({
                ...old,
                seoAlt: e.target.value,
              }));
            }}
            type="text"
            name="seoAlt"
            value={category?.seoAlt}
          />
          <br />
          <CInput
            className="w-25"
            type="number"
            name="type"
            hidden
            defaultValue={category?.type}
            placeholder="Index"
          />
          <CInput type="hidden" name="parentId" defaultValue="" />
          <div>
            <CButton
              className="px-4 mr-4 py-2"
              onClick={updateCategory}
              color="success"
              disabled={!isUpdate}
            >
              Save
            </CButton>
            <CButton
              style={{ color: "gray", borderColor: "#d9d9d9" }}
              className="px-4 py-2"
              onClick={() => {
                history.push("/categoriesList");
              }}
              variant="outline"
            >
              Back
            </CButton>
          </div>
        </div>
      </div>
      {/* for chategory list */}
      {category.identity !== "sub2Category" && (
        <>
          <h3 className="center my-4">Sub-Categories</h3>
          <CDataTable
            tableFilter
            items={childs}
            fields={[
              "Si_NO",
              "Image",
              { key: "name", _classes: "" },
              { key: "Action", label: "" },
            ]}
            hover
            striped
            loading={loading}
            itemsPerPage={100}
            clickableRows
            scopedSlots={{
              Si_NO: (item, index) => <td>{index + 1}</td>,
              Image: (item, index) => (
                <td>
                  <img
                    key={index}
                    width="40"
                    src={item.categoryImage[0]?.img}
                    alt="icon"
                  />
                </td>
              ),
              Action: (item) => (
                <>
                  {category !== "sub2Category" && (
                    <td
                      className="delete_color text-center"
                      onClick={() => {
                        history.push("/categoriesList/" + item._id);
                      }}
                    >
                      <CreateIcon />
                    </td>
                  )}
                  <td
                    className="delete_red_color"
                    onClick={() => {
                      confirmDeleteCategories(item._id);
                    }}
                  >
                    <DeleteForeverIcon />
                  </td>
                </>
              ),
            }}
          />
        </>
      )}
      {/* end table */}

      {/* modeals */}
      <CModal
        className="sm-30"
        show={updateModal && isUpdate}
        onClose={updateCategory}
      >
        <CModalHeader closeButton>COMFIRM</CModalHeader>
        <CModalBody>update</CModalBody>
        <CModalFooter>
          <CButton onClick={updateCategory} color="primary">
            Cancel
          </CButton>
          <CButton onClick={updateCategories} color="primary">
            Confirm
          </CButton>
        </CModalFooter>
      </CModal>
      {/* add new cate  */}
      <CModal show={newCategoryModal} onClose={NewCategoryModalToggle}>
        <CModalHeader closeButton>New Category</CModalHeader>
        <CModalBody>
          <h6>Category name :</h6>
          <CInput
            onChange={(e) => setNewCategoriesName(e.target.value)}
            value={newCategoriesName}
            type="text"
            name="name"
            placeholder="Enter New Categories"
          />
          <h6 className="mt-3">Category index :</h6>
          <CInput
            onChange={(e) => setNewCategoriesIndex(e.target.value)}
            value={newCategoriesIndex}
            type="number"
            name="index"
            placeholder="index"
          />
          <h6 className="mt-3">Category Page Type :</h6>
          <select
            style={{ width: "100%", height: "30px" }}
            value={newCategoriesType}
            onChange={(e) => setNewCategoriesType(e.target.value)}
            name="type"
          >
            <option value="list">List</option>
            <option value="store">Store</option>
          </select>
          <h6 className="mt-3">Category SEO Title :</h6>
          <CInput
            onChange={(e) => setSeoTitle(e.target.value)}
            value={seoTitle}
            type="text"
            name="seoDescription"
            placeholder="seoTitle"
          />
          <h6 className="mt-3">Category SEO Description :</h6>
          <CInput
            onChange={(e) => setSeoDescription(e.target.value)}
            value={seoDescription}
            type="text"
            name="seoDescription"
            placeholder="seoDescription"
          />
          <h6 className="mt-3">Category SEO Alt {"(for icon Seo)"} :</h6>
          <CInput
            onChange={(e) => setSeoAlt(e.target.value)}
            value={seoAlt}
            type="text"
            name="seoAlt"
            placeholder="seoAlt"
          />
          <h6 className="mt-3">Image :</h6>
          <CInput
            onChange={(e) => setNewCategoriesImg(e.target.files[0])}
            type="file"
            accept="image/png, image/jpeg,image/jpg,image/svg,image/webp"
            name="categoryImage"
            value={newCategoriesImg?.fileName}
          />
          {/* optional */}
          {category?.identity === "sub1Category" && (
            <div>
              <div>
                <h6 className="mt-3">Web-Banner :</h6>
                <CInput
                  onChange={(e) => setNewCategoryWBanner(e.target.files[0])}
                  type="file"
                  accept="image/png,image/jpeg,image/jpg,image/svg,image/webp"
                  name="webBanner"
                  value={newCategoryWBanner?.fileName}
                />
              </div>
              <div>
                <h6 className="mt-3">Mobile-Banner :</h6>
                <CInput
                  onChange={(e) => setNewCategoryMBanner(e.target.files[0])}
                  type="file"
                  accept="image/png,image/jpeg,image/jpg,image/svg,image/webp"
                  name="mobBanner"
                  value={newCategoryMBanner?.fileName}
                />
              </div>
            </div>
          )}

          <br />
          {event?.type === "warn" && (
            <div className="position-fixed fixed-top d-flex justify-content-center">
              <CAlert color="danger">{event.massage}</CAlert>
            </div>
          )}
        </CModalBody>
        <CModalFooter>
          <CButton onClick={addNewCategories} color="primary">
            Add
          </CButton>
        </CModalFooter>
      </CModal>
      {/* brand model */}
      <CModal className="sm-30" show={brandModal} onClose={toggleBrand}>
        <CModalHeader closeButton>New Brand</CModalHeader>
        <CModalBody>
          <div className="form-group">
            <h4>Banner</h4>
            <img
              className="mr-4"
              width="80"
              src={category?.banner?.[0]?.img}
              alt="icon"
            />
          </div>
          <p>Brand Color</p>
          <input
            className="w-25"
            type="color"
            name="Color"
            value={color}
            onChange={(e) => setColor(e.target.value)}
          />
        </CModalBody>
        <CModalFooter>
          <CButton onClick={toggleBrand} color="primary">
            Cancel
          </CButton>
          <CButton onClick={createBrand} color="primary">
            Confirm
          </CButton>
        </CModalFooter>
      </CModal>
      {event?.type === "error" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="danger">{event.massage}</CAlert>
        </div>
      )}
      {event?.type === "success" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="success">{event.massage}</CAlert>
        </div>
      )}
    </div>
  );
};

export default EditCategory;
