import { CAlert } from "@coreui/react";
import { CKEditor } from "@ckeditor/ckeditor5-react";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import { useEffect, useState } from "react";
import axios from "axios";
import { selectUser } from "src/localyStore";
import { useSelector } from "react-redux";
const DPolicy = () => {
  const [data, setData] = useState();
  const [id, setId] = useState();
  const { token } = useSelector(selectUser);
  // alert
  const [event, setEvent] = useState(null);

  const clearEvent = (ms) => {
    setTimeout(() => {
      setEvent(null);
    }, ms);
  };

  useEffect(() => {
    axios
      .get(
        process.env.REACT_APP_SERVER +
          process.env.REACT_APP_ADMIN_GET_INFOPAGE +
          "delivery-policy"
      )
      .then((res) => {
        setId(res.data.infoPages._id);
        setData(res.data.infoPages.description);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "error on server",
        });
        clearEvent(5000);
      });
  }, []);
  const Submit = () => {
    axios
      .patch(
        process.env.REACT_APP_SERVER +
          process.env.REACT_APP_ADMIN_UPDATE_INFOPAGE +
          id,
        {
          title: "delivery-policy",
          description: data,
        },
        {
          headers: {
            authorization: "Bearer " + token,
          },
        }
      )
      .then((res) => {
        setData(data);
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "error on server",
        });
        clearEvent(5000);
      });
  };
  return (
    <div>
      <CKEditor
        editor={ClassicEditor}
        data={data}
        onChange={(event, editor) => {
          const data = editor.getData();
          setData(data);
        }}
      />
      <button type="submit" class="btn btn-primary mt-4" onClick={Submit}>
        Save changes
      </button>
      {event?.type === "error" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="danger">{event.massage}</CAlert>
        </div>
      )}
      {event?.type === "success" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="success">{event.massage}</CAlert>
        </div>
      )}
    </div>
  );
};

export default DPolicy;
