import CIcon from "@coreui/icons-react";
import {
  CCol,
  CRow,
  CCard,
  CCardTitle,
  CCardBody,
  CDataTable,
  CDropdown,
  CDropdownToggle,
  CDropdownMenu,
  CDropdownItem,
  CAlert,
} from "@coreui/react";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";
import NoteAddIcon from "@material-ui/icons/NoteAdd";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { selectUser } from "../../localyStore";
import { useHistory } from "react-router-dom";
import "./Vendor.css";
const server = process.env.REACT_APP_SERVER;

const VendorProducts = () => {
  const { role, token } = useSelector(selectUser);
  const history = useHistory();
  const [loading, setLoading] = useState(true);
  const [reload, SetReload] = useState(false);
  const [products, setProducts] = useState();
  const [event, setEvent] = useState(null);

  const clearEvent = (ms) => {
    setTimeout(() => {
      setEvent(null);
    }, ms);
  };

  useEffect(() => {
    const getProductListVendor = process.env.REACT_APP_ADMIN_PRODUCT_GET_VENDOR;
    axios
      .get(server + getProductListVendor, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        dataChange(res?.data.product);
        setLoading(false);
      })
      .catch((err) => {
        setLoading(false);
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "back-end error",
        });
        clearEvent(5000);
      });
  }, [token, role, reload]);
  // change data
  const dataChange = (data) => {
    let newData = data.map((i) => ({
      _id: i._id,
      ProductImg: i.productPictures?.[0]?.img,
      name: i.name,
      price: Math.round(i.price),
      quantity: Math.round(i.quantity),
      discountPrice: Math.round(i.discountPrice),
      finalPrice: Math.round(i.finalPrice),
      slug: i.slug,
      vendor: i.createdBy.name,
      category:
        i.sub2Category?.name || i.sub1Category?.name || i.mainCategory?.name,
      realData: i,
    }));
    setProducts(newData);
  };

  //after api
  // delete products
  const deleteProduct = (id) => {
    axios
      .delete(server + process.env.REACT_APP_ADMIN_PRODUCT_DELETE_VENDOR + id, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        setEvent({
          type: "sucess",
          massage: res.data.message,
        });
        SetReload(!reload);
        clearEvent(5000);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };

  //   approve product by admin
  const approveVendorProduct = (item) => {
    item.verifiedVendors = item.createdBy._id;
    item.mainCategory = item.mainCategory._id;
    item.sub1Category = item.sub1Category._id;
    item.sub2Category = item.sub2Category._id;
    item.seoTitle = item.name;
    item.seoDescription = item.name;
    item.seoAlt = item.name;
    delete item.slug;
    delete item.createdBy;
    delete item.createdAt;
    console.log(item);

    axios
      .post(
        server + process.env.REACT_APP_ADMIN_PRODUCT_APPROVE_VENDOR,
        { ...item },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      )
      .then((res) => {
        setEvent({
          type: "sucess",
          massage: res.data.message,
        });
        SetReload(!reload);
        clearEvent(5000);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };

  return (
    <div className="productlist">
      <CRow>
        <CCol xl={12}>
          <CCard>
            <CCardTitle className="text-center mt-4">
              Pending Products
            </CCardTitle>
            <CCardBody>
              <CDataTable
                tableFilter
                columnFilter
                sorter
                pagination
                itemsPerPageSelect
                loading={loading}
                items={products}
                fields={[
                  {
                    key: "SiNo.",
                    label: "items",
                    _style: { width: "2%" },
                    sorter: false,
                    filter: false,
                  },
                  {
                    key: "ProductImg",
                    label: "image",
                    sorter: false,
                    filter: false,
                  },
                  "slug",
                  "price",
                  "discountPrice",
                  "finalPrice",
                  "category",
                  "quantity",
                  "vendor",
                  {
                    key: "Action",
                    label: "",
                    _style: { width: "6%" },
                    sorter: false,
                    filter: false,
                  },
                ]}
                hover
                striped
                itemsPerPage={10}
                // onRowClick={(item) => history.push(`/product/${item._id}`)}
                scopedSlots={{
                  "SiNo.": (item, index) => <td key={index}>{index + 1}</td>,
                  ProductImg: (item) => (
                    <td>
                      <img
                        className="productlist_img"
                        loading="lazy"
                        src={item.ProductImg}
                        alt={item.name}
                      />
                    </td>
                  ),
                  Action: (item) => (
                    <td>
                      <CDropdown>
                        <CDropdownToggle>
                          <CIcon name="cilSettings" size="2xl" />
                        </CDropdownToggle>
                        <CDropdownMenu>
                          <CDropdownItem
                            onClick={() => approveVendorProduct(item.realData)}
                          >
                            <CheckCircleIcon /> Approve
                          </CDropdownItem>
                          <CDropdownItem
                            onClick={() =>
                              history.push(`/vendor-Product/${item._id}`)
                            }
                          >
                            <NoteAddIcon /> Edit
                          </CDropdownItem>
                          <CDropdownItem
                            onClick={() => deleteProduct(item._id)}
                          >
                            <DeleteForeverIcon /> Delete
                          </CDropdownItem>
                        </CDropdownMenu>
                      </CDropdown>
                    </td>
                  ),
                }}
              />
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
      {event?.type === "error" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="danger">{event.massage}</CAlert>
        </div>
      )}
      {event?.type === "success" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="success">{event.massage}</CAlert>
        </div>
      )}
    </div>
  );
};
export default VendorProducts;
