import {
  CCard,
  CCardTitle,
  CInputGroup,
  CInputGroupText,
  CInput,
  CAlert,
} from "@coreui/react";
import { FormControlLabel, Switch } from "@material-ui/core";
import axios from "axios";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { selectUser } from "src/localyStore";
const Scratch = () => {
  const server = process.env.REACT_APP_SERVER;
  const { token } = useSelector(selectUser);
  const [prizes, setPrizes] = useState(null);
  const [isEdited, setIsEdited] = useState(false);
  const [isActive, setIsActive] = useState(true);
  // alert
  const [event, setEvent] = useState(null);

  const clearEvent = (ms) => {
    setTimeout(() => {
      setEvent(null);
    }, ms);
  };

  useEffect(() => {
    axios
      .get(server + process.env.REACT_APP_ADMIN_GET_GAME_SCRACH_PRICE, {
        headers: {
          authorization: "Bearer " + token,
        },
      })
      .then((res) => {
        setPrizes(res.data.scratchWinGame[0].totalPrize);
        setIsActive(res.data.scratchWinGame[0].isActive);
        setIsEdited(false);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "server-error",
        });
        clearEvent(5000);
      });
  }, [token]);
  // edit prize and update setState
  const editPisze = (value, old) => {
    prizes?.find((o, i) => {
      if (o._id === old._id) {
        prizes[i] = {
          prize: value,
          prize_limits: o.prize_limits,
          prizeImage: o.prizeImage,
          _id: o._id,
        };
      }
      return null;
    });
    setIsEdited(true);
    setPrizes(prizes);
  };
  // edit prize limit and update useState
  const editPiszeLimit = (value, old) => {
    prizes?.find((o, i) => {
      if (o._id === old._id) {
        prizes[i] = {
          prize: o.prize,
          prize_limits: value,
          prizeImage: o.prizeImage,
          _id: o._id,
        };
      }
      return null;
    });
    setIsEdited(true);
    setPrizes(prizes);
  };
  // if anythings edited then update to server
  const updatePrizes = () => {
    axios
      .post(
        server + process.env.REACT_APP_ADMIN_CREATE_GAME_SCRACH_PRICE,
        {
          totalPrize: prizes,
          isActive: isActive,
        },
        {
          headers: {
            authorization: "Bearer " + token,
          },
        }
      )
      .then((response) => {
        setPrizes(response.data.updateScratchWin.totalPrize);
        setIsEdited(false);
        setEvent({
          type: "success",
          massage: response.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "server-error",
        });
        clearEvent(5000);
      });
  };
  return (
    <main>
      <CCard className="p-4">
        <div className="d-flex align-items-center justify-content-between">
          <CCardTitle>
            <div className="d-flex align-items-center">
              <p className="mr-4 mt-2"> Scratch and Win</p>
              <FormControlLabel
                value={isActive}
                control={
                  <Switch
                    color="success"
                    checked={isActive}
                    onChange={(e) => {
                      setIsEdited(true);
                      setIsActive(e.target.checked);
                    }}
                  />
                }
                label={isActive ? "Active" : "InActive"}
                labelPlacement="end"
              />
            </div>
          </CCardTitle>
        </div>
        <hr />
        <div className="d-flex justify-content-start">
          <div className="d-flex w-25 ml-2">
            <h3>Prize</h3>
          </div>
          <h3 className="ml-4">Prize Limit</h3>
        </div>
        <hr />
        <div className="d-flex flex-row justify-content-start align-items-center">
          <div className="flex-row w-25 mr-4 align-items-center">
            {prizes?.map((item) => (
              <CInputGroup className="mb-3">
                <CInputGroupText id="basic-addon3">Slot 1</CInputGroupText>
                <CInput
                  defaultValue={item.prize}
                  onChange={(e) => editPisze(e.target.value, item)}
                />
              </CInputGroup>
            ))}
          </div>

          <div className="flex-row w-25 align-items-center">
            {prizes?.map((item) => (
              <CInputGroup className="mb-3">
                <CInputGroupText id="basic-addon3">Limit</CInputGroupText>
                <input
                  type="number"
                  defaultValue={item.prize_limits}
                  onChange={(e) => editPiszeLimit(e.target.value, item)}
                />
              </CInputGroup>
            ))}
          </div>
        </div>
        <hr />
        <button
          disabled={!isEdited}
          type="button"
          class="btn btn-ghost-dark btn-lg"
          onClick={updatePrizes}
        >
          Submit
        </button>
      </CCard>
      {event?.type === "error" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="danger">{event.massage}</CAlert>
        </div>
      )}
      {event?.type === "success" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="success">{event.massage}</CAlert>
        </div>
      )}
    </main>
  );
};

export default Scratch;
