import React from "react";
import Career from "./pages/policy/Career";
import Company from "./pages/policy/Company";
import DPolicy from "./pages/policy/DPolicy";
import Policy from "./pages/policy/Policy";
import Terms from "./pages/policy/Terms";
import Brand from "./views/Brand/Brand";
import EditCategory from "./views/categories/EditCategory";
import AssineDriver from "./views/driver/AssineDriver";
import DriverList from "./views/driver/DriverList";
import Quiz from "./views/games/Quiz";
import Scratch from "./views/games/Scratch";
import Spain from "./views/games/SpinAndWin";
import Surprise from "./views/games/Surprise";
import VendorProducts from "./views/Manufacturer/VendorProducts";
import OfferPage from "./views/offers/Offer";
import Offers from "./views/offers/Offers";
import EditPendingProduct from "./views/product/EditPendingProduct";

const GameResult = React.lazy(() => import("./views/games/GameResult"));

const Register = React.lazy(() => import("./pages/register/Register"));

const Setting = React.lazy(() => import("./views/setting/Setting"));

const addProducts = React.lazy(() => import("./views/product/Addproduct"));

const Navs = React.lazy(() => import("./views/base/navs/Navs"));

const Dashboard = React.lazy(() => import("./views/dashboard/Dashboard"));

const Notification = React.lazy(() =>
  import("./views/notifications/Notification")
);
const Shipping = React.lazy(() => import("./views/ShippingMethod/Shipping"));
const Orders = React.lazy(() => import("./views/orders/Orders"));
const Order = React.lazy(() => import("./views/orders/Order"));
const Users = React.lazy(() => import("./views/users/Users"));
const User = React.lazy(() => import("./views/users/User"));
const Admin = React.lazy(() => import("./views/users/Admin"));
const Profile = React.lazy(() => import("./views/Profile/Profile"));
const ProductList = React.lazy(() => import("./views/product/productLists"));
const editProduct = React.lazy(() => import("./views/product/editProduct"));
const ListCategories = React.lazy(() =>
  import("./views/categories/ListCategories")
);
const Manufacturer = React.lazy(() =>
  import("./views/Manufacturer/manufacturer")
);
const manufacturelist = React.lazy(() =>
  import("./views/Manufacturer/manufacturers")
);
const Page404 = React.lazy(() => import("./pages/page404/Page404"));
const Coupon = React.lazy(() => import("./views/coupon/Coupon"));
const routes = [
  { path: "/", name: "Dashboard", component: Dashboard, exact: true },
  { path: "/register", exact: true, name: "Register", component: Register },
  {
    path: "/categoriesList",
    exact: true,
    name: "categoriesList",
    component: ListCategories,
  },
  {
    path: "/categoriesList/:id",
    exact: true,
    name: "Category",
    component: EditCategory,
  },
  {
    path: "/add Products",
    exact: true,
    name: "addProducts",
    component: addProducts,
  },
  {
    path: "/productlist",
    name: "Products",
    component: ProductList,
    exact: true,
  },
  {
    path: "/vendor-Product/:id",
    name: "Pending",
    component: EditPendingProduct,
    exact: true,
  },
  {
    path: "/shipping-method",
    name: "Shipping",
    component: Shipping,
    exact: true,
  },
  {
    path: "/productlist/:id",
    name: "editProduct",
    component: editProduct,
    exact: true,
  },
  { path: "/orders", name: "Orders", component: Orders, exact: true },
  {
    path: "/order/:orderid",
    name: "Order",
    component: Order,
    exact: true,
  },
  { path: "/notification", name: "Notification", component: Notification },
  { path: "/Profile", name: "Profile", component: Profile },

  { path: "/base/navs", name: "Navs", component: Navs },
  {
    path: "/vendor-List",
    name: "vendor-List",
    component: manufacturelist,
  },
  {
    path: "/vendor-Product",
    name: "vendor-Product",
    component: VendorProducts,
  },
  {
    path: "/vendor/:id",
    name: "vendor",
    component: Manufacturer,
  },
  {
    path: "/driver-list",
    name: "Driver",
    component: DriverList,
  },
  {
    path: "/assign-driver",
    name: "Assign",
    component: AssineDriver,
  },
  {
    path: "/create-brand",
    name: ["Create-brands"],
    component: Brand,
  },

  {
    path: "/game/spin-win",
    name: ["games", "Spin"],
    component: Spain,
  },
  {
    path: "/game/surprise-box",
    name: ["games", "Suprise"],
    component: Surprise,
  },
  {
    path: "/game/scratch",
    name: ["games", "Scratch"],
    component: Scratch,
  },
  {
    path: "/game/quiz",
    name: "quiz",
    component: Quiz,
  },
  {
    path: "/game/results",
    name: "results",
    component: GameResult,
  },
  {
    path: "/create-offers",
    name: "OfferPage",
    component: OfferPage,
  },
  {
    path: "/offers",
    name: "Offers",
    component: Offers,
  },
  { path: "/coupon", exact: true, name: "Cupon", component: Coupon },
  { path: "/users", exact: true, name: "Users", component: Users },
  { path: "/users/:id", exact: true, name: "User Details", component: User },
  { path: "/admin/:id", exact: true, name: "User Details", component: Admin },
  { path: "/setting", exact: true, name: "setting", component: Setting },
  // pages terms-policy...
  {
    path: "/terms-conditions",
    exact: true,
    name: "terms-conditions",
    component: Terms,
  },
  {
    path: "/privacy-policy",
    exact: true,
    name: "private-policy",
    component: Policy,
  },
  {
    path: "/company-info",
    exact: true,
    name: "company",
    component: Company,
  },
  {
    path: "/delivery-policy",
    exact: true,
    name: "Delivery",
    component: DPolicy,
  },
  {
    path: "/career",
    exact: true,
    name: "career",
    component: Career,
  },
  { path: "*", name: "notFund", component: Page404 },
];

export default routes;
