import axios from "axios";

const ImageHandler = (id, name, replace, img, deleteId, token, type) => {
  const formData = new FormData();
  formData.append("_id", id);
  formData.append("name", name);
  if (replace) {
    formData.append("replace", replace);
  }
  if (img) {
    formData.append("addNewImage", img);
  }
  if (deleteId) {
    formData.append("deleteOldImage", deleteId);
  }
  if (type) {
    formData.append("type", type);
  }
  return axios
    .post(process.env.REACT_APP_SERVER + "/api/image-handler", formData, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
    .then((res) => {
      console.log(res);
      return "sucess";
    })
    .catch((err) => {
      console.log(err);
      return null;
    });
};

export { ImageHandler };
