import {
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CDataTable,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CRow,
  CAlert,
} from "@coreui/react";
import axios from "axios";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { selectUser } from "src/localyStore";

const server = process.env.REACT_APP_SERVER;

const OfferPage = () => {
  const [model, setModal] = useState(false);
  const [reload, setReload] = useState(false);
  const [offers, setOffers] = useState([]);
  const [flashSales, setFlashSale] = useState([]);
  // const [list, setList] = useState([]);
  const [loading, setLoading] = useState(true);
  const { token } = useSelector(selectUser);
  const [updateValue, setUpdateValue] = useState({
    _id: "",
    offerTitle: "",
    flashSales: "",
    isActive: true,
    expiredAt: "",
    createdAt: "",
    banner: "",
  });
  // alert
  const [event, setEvent] = useState(null);

  const clearEvent = (ms) => {
    setTimeout(() => {
      setEvent(null);
    }, ms);
  };
  // load
  useEffect(() => {
    //  for offers
    const getDataAsync = async () => {
      const flashSaleData = await axios.get(
        server + process.env.REACT_APP_ADMIN_GET_FLASH_SALE,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      const offersData = await axios.get(
        server + process.env.REACT_APP_ADMIN_GET_OFFERS,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      setFlashSale([
        {
          ...flashSaleData.data.flashSales[0],
          offerTitle: flashSaleData.data.flashSales[0].flashSalesTitle,
        },
      ]);
      setOffers(offersData.data.offers);
      setLoading(false);
    };
    getDataAsync();
  }, [token, reload]);

  // crate new offers and updATE
  const createOffer = (e) => {
    e.preventDefault();
    let url;
    const form = new FormData(e.target);
    if (updateValue.flashSalesTitle) {
      url = process.env.REACT_APP_ADMIN_CREATE_FLASH_SALE;
    } else {
      url = process.env.REACT_APP_ADMIN_CREATE_OFFERS;
    }
    axios
      .post(server + url, form, {
        headers: {
          authorization: "Bearer " + token,
        },
      })
      .then((res) => {
        setReload(!reload);
        setModal(false);
        setUpdateValue({
          _id: "",
          offerTitle: "",
          flashSales: "",
          isActive: true,
          expiredAt: "",
          createdAt: "",
          banner: "",
        });
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        setModal(false);
        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };

  const update = (item) => {
    setUpdateValue(item);
    setModal(!model);
  };
  return (
    <div>
      <CRow>
        <CCol xl={12}>
          <div className="d-flex justify-content-center mb-3">
            {/* <button type="button" onClick={toggle} className="btn btn-primary">
              Create New Offer
            </button> */}
            <CModal className="sm-30" show={model}>
              <form onSubmit={createOffer}>
                <CModalHeader>Please Provide the Information</CModalHeader>
                <CModalBody>
                  <div className="form-group row">
                    <label
                      htmlFor="offerName"
                      className="col-sm-3 col-form-label font-weight-bold font-lg"
                    >
                      {updateValue.flashSalesTitle
                        ? "flashSalesTitle"
                        : "Offer Name"}
                    </label>
                    <div className="col-sm-10">
                      <input
                        type="text"
                        name={
                          updateValue.flashSalesTitle
                            ? "flashSalesTitle"
                            : "offerTitle"
                        }
                        value={
                          updateValue.flashSalesTitle ?? updateValue?.offerTitle
                        }
                        onChange={(e) => {
                          if (updateValue.flashSalesTitle) {
                            setUpdateValue((old) => ({
                              ...old,
                              flashSalesTitle: e.target.value,
                            }));
                          } else {
                            setUpdateValue((old) => ({
                              ...old,
                              offerTitle: e.target.value,
                            }));
                          }
                        }}
                      />
                    </div>
                  </div>
                  <div className="form-group row ">
                    <label
                      htmlFor="schedule"
                      className="col-sm-3 col-form-label font-weight-bold font-lg"
                    >
                      Schedule
                    </label>
                    <div className="col-sm-10">
                      Start Date
                      <input
                        className="form-control"
                        name="createdAt"
                        value={updateValue?.createdAt}
                        type="datetime-local"
                        onChange={(e) =>
                          setUpdateValue((old) => ({
                            ...old,
                            createdAt: e.target.value,
                          }))
                        }
                      />
                    </div>
                    <div className="col-sm-10">
                      End Date
                      <input
                        type="datetime-local"
                        className="form-control"
                        name="expiredAt"
                        value={updateValue?.expiredAt}
                        onChange={(e) =>
                          setUpdateValue((old) => ({
                            ...old,
                            expiredAt: e.target.value,
                          }))
                        }
                      />
                    </div>
                  </div>
                  <fieldset className="form-group">
                    <div className="row">
                      <legend className="col-form-label col-sm-3 pt-0 font-lg ">
                        Set Status
                      </legend>
                      <div className="col-sm-10">
                        <div className="form-check">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="isActive"
                            value="true"
                            checked={updateValue?.isActive}
                            onChange={(e) =>
                              setUpdateValue((old) => ({
                                ...old,
                                isActive: true,
                              }))
                            }
                          />
                          <span className="form-check-label">Active</span>
                        </div>
                        <div className="form-check">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="isActive"
                            value="false"
                            checked={!updateValue?.isActive}
                            onChange={(e) =>
                              setUpdateValue((old) => ({
                                ...old,
                                isActive: false,
                              }))
                            }
                          />
                          <span className="form-check-label">Inactive</span>
                        </div>
                      </div>
                    </div>
                  </fieldset>
                  <div className="form-group row">
                    <div className="col-sm-3">
                      Add Banner
                      <input
                        type="file"
                        name="banner"
                        accept="image/png, image/jpeg,image/jpg,image/svg,image/webp"
                        // onChange={(e) => setfile(e.target.files[0])}
                      />
                    </div>
                  </div>
                </CModalBody>
                <CModalFooter>
                  <div className="modal-footer">
                    <button
                      type="button"
                      className="btn btn-secondary"
                      onClick={() => setModal(false)}
                    >
                      Close
                    </button>
                    <button
                      type="submit"
                      className="btn btn-primary"
                      disabled={!updateValue._id}
                    >
                      Save changes
                    </button>
                  </div>
                </CModalFooter>
                <input
                  type="text"
                  hidden
                  name="offerId"
                  readOnly
                  value={updateValue._id}
                />
              </form>
            </CModal>
          </div>
          <CCard>
            <CCardHeader>Offers</CCardHeader>
            <CCardBody>
              <CDataTable
                fields={[
                  "SiNo",
                  { key: "offerTitle", _classes: "font-weight-bold" },
                  "createdAt",
                  "expiredAt",
                  {
                    key: "isActive",
                    label: "Status",
                    _style: { width: "15%" },
                    sorter: false,
                    filter: false,
                  },
                ]}
                loading={loading}
                items={flashSales.concat(offers)}
                sorter
                columnFilter
                tableFilter
                hover
                itemsPerPage={20}
                clickableRows
                onRowClick={(item) => update(item)}
                scopedSlots={{
                  SiNo: (item, index) => <td key={index}>{index + 1}</td>,
                  isActive: (item, index) => (
                    <td
                      key={index}
                      className={`${
                        item.isActive ? "text-success" : "text-danger"
                      }`}
                    >
                      {item.isActive ? "Active" : "InActive"}
                    </td>
                  ),
                }}
              />
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
      {event?.type === "error" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="danger">{event.massage}</CAlert>
        </div>
      )}
      {event?.type === "success" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="success">{event.massage}</CAlert>
        </div>
      )}
    </div>
  );
};

export default OfferPage;
