  //             prizeImage: data.location,
  //             _id: o._id,
  //           };
  //         }
  //       });
  //       setIsEdited(true);
  //       setPrizes(prizes);
  //       setLoading(false);
  //     })
  //     .catch((err) => {
  //       alert(err.message);
  //       setLoading(false);
  //     });
  // };
