import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { selectSidebarShow, selectUser } from "../localyStore";
import {
  CCreateElement,
  CSidebar,
  CSidebarBrand,
  CSidebarNav,
  CSidebarNavDivider,
  CSidebarNavTitle,
  CSidebarMinimizer,
  CSidebarNavDropdown,
  CSidebarNavItem,
} from "@coreui/react";
// sidebar nav config
import navigation from "./_nav";

const TheSidebar = () => {
  const dispatch = useDispatch();
  const User = useSelector(selectUser);
  const show = useSelector(selectSidebarShow);
  const role = User.role;

  if (role === "admin") {
    return (
      <CSidebar
        show={show}
        onShowChange={(val) => dispatch({ type: "set", sidebarShow: val })}
      >
        <CSidebarBrand className="d-md-down-none" to="/">
          <h2 style={{ textTransform: "uppercase" }}>{role}</h2>
        </CSidebarBrand>
        <CSidebarNav>
          <CCreateElement
            items={navigation}
            components={{
              CSidebarNavDivider,
              CSidebarNavDropdown,
              CSidebarNavItem,
              CSidebarNavTitle,
            }}
          />
        </CSidebarNav>
        <CSidebarMinimizer className="c-d-md-down-none" />
      </CSidebar>
    );
  }
  if (role === "vendor") {
    return (
      <CSidebar
        show={show}
        onShowChange={(val) => dispatch({ type: "set", sidebarShow: val })}
      >
        <CSidebarBrand className="d-md-down-none" to="/">
          <h2 style={{ textTransform: "uppercase" }}>{role}</h2>
        </CSidebarBrand>
        <CSidebarNav>
          <CCreateElement
            items={[navigation[0], navigation[2], navigation[3]]}
            components={{
              CSidebarNavDivider,
              CSidebarNavDropdown,
              CSidebarNavItem,
              CSidebarNavTitle,
            }}
          />
        </CSidebarNav>
        <CSidebarMinimizer className="c-d-md-down-none" />
      </CSidebar>
    );
  }
};

export default React.memo(TheSidebar);
