import React from "react";
import {
  CBadge,
  CDropdown,
  CDropdownItem,
  CDropdownMenu,
  CDropdownToggle,
  CImg,
} from "@coreui/react";
import CIcon from "@coreui/icons-react";
import CreateIcon from "@material-ui/icons/Create";
import { useHistory } from "react-router-dom";
import { useCookies } from "react-cookie";
import { useSelector, useDispatch } from "react-redux";
import { selectUser, loggout } from "../localyStore";

import axios from "axios";

const TheHeaderDropdown = () => {
  const history = useHistory();
  const [, setCookie] = useCookies(["user"]);
  const dispatch = useDispatch();
  const { role, token } = useSelector(selectUser);

  const AdminLogout = () => {
    axios
      .post(
        process.env.REACT_APP_SERVER + process.env.REACT_APP_ADMIN_LOGOUT,
        {},
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      )
      .then(() => {
        dispatch(loggout());
        setCookie("user", null);
        localStorage.clear();
        window.location.href = "/";
      })
      .catch((err) => {
        alert(err);
      });
  };

  return (
    <CDropdown inNav className="c-header-nav-items mx-2" direction="down">
      <CDropdownToggle className="c-header-nav-link" caret={false}>
        <div className="c-avatar">
          <CImg
            src={
              "https://th.bing.com/th/id/OIF.WREm3pqUiJRS6HMGOgQRBA?pid=ImgDet&rs=1"
            }
            className="c-avatar-img"
            alt="Avarar Icon"
          />
        </div>
      </CDropdownToggle>
      <CDropdownMenu className="pt-0" placement="bottom-end">
        {role === "admin" ? (
          <>
            <CDropdownItem
              header
              tag="div"
              color="light"
              className="text-center"
            >
              <strong>Account</strong>
            </CDropdownItem>
            <CDropdownItem onClick={() => history.push("/register")}>
              <CreateIcon style={{ marginRight: "7px" }} />
              Register
              <CBadge color="info" className="mfs-auto">
                New
              </CBadge>
            </CDropdownItem>
          </>
        ) : (
          ""
        )}
        <CDropdownItem header tag="div" color="light" className="text-center">
          <strong>Settings</strong>
        </CDropdownItem>
        <CDropdownItem onClick={() => history.push("/profile")}>
          <CIcon name="cil-user" className="mfe-2" />
          Profile
        </CDropdownItem>
        {role === "admin" && (
          <CDropdownItem onClick={() => history.push("/setting")}>
            <CIcon name="cil-settings" className="mfe-2" />
            Settings
          </CDropdownItem>
        )}
        <CDropdownItem divider />
        <CDropdownItem onClick={AdminLogout}>
          <CIcon name="cil-lock-locked" className="mfe-2" />
          Logout Admin
        </CDropdownItem>
      </CDropdownMenu>
    </CDropdown>
  );
};

export default TheHeaderDropdown;
